body, html {
    color: white;
    font-family:"Dejavu Sans Mono";
    background: black;
    height: 100%;
    width: 100%;
}
#root {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vw;
    height: 100%;
}

.content, .container  {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vw;
}
.container  {
    flex: 1 0 auto;
}

.footer a, .header a {
    color: white;
    margin: 40px;
    text-decoration: none;
    font-size: 1.5em;
    font-weight: 500;
}

.footer, .header {
    margin: 20px;
}

.footer {
    flex-shrink: 0;
    padding: 40px;
    
}

#nav-icon {
    color: white;
    font-size: 3em;
    font-weight: 700;
}

.image-gallery {
    height: 100%;
    width: 100%;
    background: white;
    display: flex;
    flex-direction: column;
    align-items: center;
}

h1 {
    font-size: 3em;
    font-weight: 700;
}

.how, .contact {
    width: 500px; }
